.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background-image: url('https://www.bebengskitchen.com/images/resources/bebengskitchen.jpg');
  background-position: right;
  background-repeat: no-repeat;
  background-size: cover;
}
.modal {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  width: 400px;
  border: 1px solid #bbcef2;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: 3px 3px 10px;
}

.logo_container {
  display: flex;
  flex-direction: column;
  margin: 10px;
  align-items: center;
  border-radius: 50%;
}

.logo {
  width: auto;
  height: 100px;
  margin: 5px 5px 0px 5px;
  border-radius: 50%;
}

.company_name {
  font-family: 'Kaushan Script', cursive;
  color: #374151;
  font-size: 36px;
}

.form_container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 100%;
  border-radius: 5px;
}

.input_container {
  display: flex;
  flex-direction: column;
  position: relative;
}

.input_container_label {
  font-size: 0.8rem;
  padding-left: 5px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.input_container_input {
  font-size: 1rem;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid gray;
}

.button_container {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
}

.button_primary {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
  transition: all 0.15s ease-in-out;
  width: 100%;
  border-radius: 25px;
  height: 40px;
}

.button_primary:disabled {
  cursor: default;
  color: #282829;
  background-color: #ababac;
}

.button_primary:hover {
  color: #fff;
  background-color: #0069d9;
  border-color: #0062cc;
  text-decoration: none;
}

.link_container {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin: 10px 0px;
}

.nav_link {
  font-size: smaller;
  transition: all 0.15s ease-in-out;
}

.nav_link:hover {
  color: #0069d9;
}

@media (max-width: 400px) {
  .modal {
    padding: 10px;
    width: 90%;
  }
}
